
div#wrapper {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1 {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#container {
  max-width: 840px;
  margin-left: auto;
  margin-right: auto;
}
#actions {
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
#actions .btn {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 2px;
}
#actions .btn:hover {
  color: white;
  background: #ccc;
}
#cnv-logo {
  border: 1px solid #eee;
  margin: 15px auto;
  -moz-box-shadow:    3px 3px 3px 3px #ddd;
  -webkit-box-shadow: 3px 3px 3px 3px #ddd;
  box-shadow:         3px 3px 3px 3px #ddd;
}
#image-holder {
  display: none;
}
